<template>
  <div class="search">
    <van-nav-bar
    title="搜索"
    left-text="主页"
    left-arrow
    @click-left="$router.push('/home')"
    />
     <van-search
      show-action
      placeholder="请输入搜索关键词"
      shape="round"
      :autofocus="true"
      v-model="searchInput"
      @search="handleSearch"
    >
      <template #action>
        <van-button type="info" @click="handleSearch">搜索</van-button>
      </template>
    </van-search>
    <div class="history">
      <span>历史</span>
      <van-icon name="delete-o" @click="deleteHistory"></van-icon>
    </div>
    <div class="list">
      <div class="list-item" v-for="item in searchHistory" :key="item" @click="handleSearchClick(item)">{{item}}</div>
    </div>
  </div>
</template>

<script>
import searchHistoryStorage from '@/utils/searchHIstoryStorage.js'
import { Toast } from 'vant'
export default {
  name: 'SearchIndex',
  data () {
    return {
      searchHistory: searchHistoryStorage.getStorage(),
      searchInput: ''
    }
  },
  methods: {
    handleSearch () {
      // console.log('clicked search')
      // console.log(typeof searchHistoryStorage.getStorage())
      if (this.searchInput === '') {
        // console.log('unexpected value of empty')
        Toast.fail({
          message: '请输入关键字'
        })
        return
      }
      const index = this.searchHistory.indexOf(this.searchInput)
      // console.log(index)
      if (index === -1) {
        this.searchHistory.unshift(this.searchInput)
      } else {
        const temp = this.searchHistory[index]
        this.searchHistory.splice(index, 1)
        this.searchHistory.unshift(temp)
      }
      searchHistoryStorage.setStorage(this.searchHistory)
      this.$router.push({
        path: `/searchList?search=${this.searchInput}`
      })
    },
    deleteHistory () {
      searchHistoryStorage.removeStorage()
      this.searchHistory = []
    },
    handleSearchClick (item) {
      const index = this.searchHistory.indexOf(item)
      // console.log(index)
      this.searchHistory.splice(index, 1)
      this.searchHistory.unshift(item)
      searchHistoryStorage.setStorage(this.searchHistory)
      this.$router.push({
        path: '/searchList',
        query: {
          search: item
        }
      })
    }
  }
}
</script>

<style scoped>
  .history{
    position: relative;
    margin: 10px auto;
    background-color: #c37777;
  }
  .history span{
    position: absolute;
    left: 10px;
    top: 0;
  }
  .history .van-icon{
    position: absolute;
    right: 10px;
    top: 0;
  }
 .list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 0 10px;
    gap: 5%;
    margin-top: 40px;
  }
  .list-item {
    width: 20%;
    text-align: center;
    padding: 7px;
    line-height: 15px;
    border-radius: 50px;
    background: #fff;
    font-size: 13px;
    border: 1px solid #efefef;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-bottom: 10px;
  }
</style>
